import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';
import { of } from 'rxjs/observable/of';
import { delay, share } from 'rxjs/operators';
import { MarkerService } from '../Services/marker.service';
import { Marker } from '../Models/Marker';
import { MapComponent } from '../component/map/map.component';
import { google } from '@agm/core/services/google-maps-types';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';

@Component({
  selector: 'app-trial',
  templateUrl: './trial.component.html',
  styleUrls: ['./trial.component.css']
})
export class TrialComponent implements OnInit {
  @ViewChild ("mapPage") mapPage:MapComponent;

  country: string;
  state: string;
  bank: string;
  banktype: string;
  markers1: Marker[] = [];
  Lat: string;
  Lng: string;
  orderflag: boolean;
 

  States = []; 
  States1 = ['Andaman and Nicobar Islands', 'Andra Pradesh', 'Arunachal Pradesh', 'Assam',
  'Bihar', 'Chhattisgarh', 'Chandigarh', 'Dadar and Nagar Haveli', 'Daman and Diu', 'Delhi', 'Goa',
  'Gujarat', 'Haryana', 'Himachal Pradesh', 'Jammu and Kashmir', 'Jharkhand', 'Karnataka', 'Kerala',
  'Lakshadeep', 'Madya Pradesh', 'Maharashtra', 'Manipur', 'Meghalaya', 'Mizoram', 'Nagaland', 'Orissa',
  'Pondicherry', 'Rajasthan', 'Sikkim', 'Tamil Nadu', 'Tripura', 'Uttaranchal', 'Uttar Pradesh', 'West Bengal'];
  States2 = ['Alabama','New Jersey','New Mexico','New York','Ohio'] 
  Countries = ['India','USA'];
  Banks = [];
  Banks1 = ['Bank Atm1','Bank Atm2','Bank Atm3','Bank Atm4'];
  Banks2 = ['Bank Atm1','Bank Atm2','Bank Atm3','Bank Atm4'];
  banktypes = ['ATM','Branch','Branch & ATM'];

  data: Array<any>;
  // temp: Observable<Array<any>>
  temp: Array<any>;

  constructor(private marServ:MarkerService) {
    this.orderflag = true;

   }

  
  CountrySelect(cn){
    console.log (cn);
    if (cn=='India')
    {
       this.States= this.States1;
       this.Banks= this.Banks1;
    }
       
       else if (cn=='USA')
       {
        this.States= this.States2;
        this.Banks= this.Banks2;
       }
          
    else
    {
      this.States= [];
      this.Banks= [];
    }

  }
myFunction() {
    var x = document.getElementById("myDIV");
    if (x.style.display === "none") {
        x.style.display = "block";
    } else {
        x.style.display = "none";
    }
}

fetchBankData(country,state,bankname,banktype){

  this.temp = [];
  this.markers1=[];
  this.mapPage.markers = [];
 
  this.marServ.getallMarkers({Country:this.country,State:this.state,Bankname:this.bank,BankType:this.banktype}).subscribe((res) => {
 
    let i = 0;
    this.temp=res;
        
    res.forEach(element => {
      i=i+1;
      var m = new Marker();
      m.lat=element.lat;
      m.lng=element.lng;
      m._id=element._id;
      m.draggable = element.draggable;
      m.label = i+"";
      m.info = element.bankname + " ("+ element.banktype + ") "+ element.state + " "+ element.country ;
      this.markers1.push(m);      
    }); 
    this.mapPage.markers=this.markers1;
    console.log(this.mapPage.markers);
  });

  this.orderflag = false; 
} 

getlocation(data){
this.mapPage.lat=data.lat;
this.mapPage.lng=data.lng;

}

ngOnInit() {
  
}

}



