import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { MouseEvent as AGMMouseEvent } from '@agm/core';
import { MarkerService } from '../../Services/marker.service';
import { Marker } from '../../Models/Marker';

@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css']
})

export class MapComponent implements AfterViewInit{

  @ViewChild("map") map:ElementRef;
  title: string = 'My first AGM project';
  lat: number = 22.5856595;
  lng: number = 88.4875224;
  clat:number;
  clong:number;
  cradius:number;
  zoom: number = 8;
  markers: Marker[];
  mapdata: any;
  

ngAfterViewInit(){
  
}


  constructor(private mserv:MarkerService){
    this.getallMarkers();
    
  }
 
  clickedMarker(label: string, index: number) {
    console.log(`clicked the marker: ${label || index}`)
  }

   mapClicked($event: AGMMouseEvent) {
    this.mserv.markers1.push({
      _id:"",
      lat: $event.coords.lat,
      lng: $event.coords.lng,
      draggable: true,
      info:"lat " + $event.coords.lat + ',' + "lng " + $event.coords.lng
    });
  } 
  

  markerDragEnd(m: Marker, $event: AGMMouseEvent) {
    console.log('dragEnd', $event.coords.lat,$event.coords.lng);
  }


  circleDragEnd($event: AGMMouseEvent) {
    this.clat = $event.coords.lat;
    this.clong = $event.coords.lng;    
  }

  radiusEnd($event : number ){
    this.cradius = $event;
  }

  getallMarkers()
  {
  this.mserv.getallMarkers({}).subscribe(e=>
  {
    this.markers= e;
    let i = 0;
    e.forEach(element => {
      i=i+1;
      var m = new Marker();
      m.lat=element.lat;
      m.lng=element.lng;
      m._id=element._id;
      m.draggable = element.draggable;
      m.label = i+"";
      m.info = element.bankname + " ("+ element.banktype + ") "+ element.state + " "+ element.country ;
      this.markers.push(m);      
    }); 
    
  });
 
   console.log ("first time",this.markers);
  }

  
/*
  markers: marker[] = [
    {
		  lat: 22.4756595,
		  lng: 88.4875224,
		  label: 'A',
      draggable: false,
      info:"Hospital ABC"
    },
    
    {
		  lat: 22.6956595,
		  lng: 88.4875224,
		  label: 'B',
      draggable: false,
      info:"School ABC"
    }

  ]
*/

  
}


